/*styles for flashcards need to figure out how to impliment into App.scss */
.card-grid {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 1rem;
  gap: 1rem; }

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: .25rem;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.3);
  background-color: #a199c9;
  transform-style: preserve-3d;
  transition: 150ms;
  cursor: pointer;
  transform: perspective(1000px) rotateY(0) translateY(0);
  transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0)); }

#flashcard {
  background-color: #a199c9; }

.card:hover {
  --translate-y: -2px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.5); }

.card.flip {
  --rotate-y: 180deg; }

.card .front {
  left: 0; }

.card .front,
.card .back {
  position: absolute;
  padding: 1rem;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden; }

.card .back {
  transform: rotateY(180deg); }

.flashcard-options {
  margin-top: .5rem; }

.flashcard-option {
  margin-top: .25rem;
  color: #555;
  font-size: .75rem;
  transition: 0.4s linear all;
  text-align: center; }

.flashcard-option span {
  transition: 0.4s linear all; }

.flashcard-option span:hover {
  background-color: dodgerblue;
  color: #fff;
  border-radius: .5em; }

.flashcard-option:first-child {
  margin-top: 0; }

